<template>
  <div class="params">
    <div id="leixin">
      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline">
          <el-form-item label="库存组织" size="mini" prop="company_id">
            <el-select clearable v-model="params.company_id" placeholder="请选择" @change="companyChange" filterable @focus="companyListInvok">
              <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
                <span :class="item.type==1 ? 'optionStyle': ''">{{ item.company_name }}</span>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="业务日期" size="mini">
            <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="单据号" size="mini">
            <el-input v-model="params.outstock_sn" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="部门" size="mini" prop="department_id">
            <el-select clearable v-model="params.department_id" placeholder="请选择" filterable @change="departmentChange" @focus="DepartmentListInvok">
              <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="审核状态" size="mini">
            <el-select clearable v-model="params.auditor_status" placeholder="请选择">
              <el-option label="全部" :value="0">全部</el-option>
              <el-option label="未审核" :value="1">未审核</el-option>
              <el-option label="已审核" :value="2">已审核</el-option>
              <el-option label="作废" :value="3">作废</el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="客户名称" prop="customer_id" size="mini">
            <el-select v-model="params.customer_id" filterable placeholder="请输入" clearable @focus="clienteleListInvok" @change="customerListChange" :loading="loading" remote :remote-method="clienteleListInvok">
              <ul class="customer customer-ul4 customer-ul2">
                <li>客户编号</li>
                <li>客户名称</li>
                <li>联系人</li>
                <li>联系电话</li>
                <li>区域</li>
              </ul>
              <el-option v-for="item in customerList" :key="item.customer_id" :label="item.customer_name" :value="item.customer_id">
                <ul class="customer customer-ul4">
                  <li :title="item.customer_id">{{ item.customer_id }}</li>
                  <li :title="item.customer_name">{{ item.customer_name }}</li>
                  <li :title="item.contact_name">{{item.contact_name}}</li>
                  <li :title="item.customer_mobile">{{item.customer_mobile}}</li>
                  <li>{{item.province_name}}/{{item.city_name}}/{{item.area_name}}</li>
                </ul>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="add_delet">
        <el-button icon="el-icon-circle-plus-outline" type="primary" @click="addF" class="addBtn" size="mini" v-if="authority.b_add==1">新增</el-button>
        <el-button plain icon="el-icon-delete" @click="deletes" size="mini" v-if="authority.b_del==1">删除</el-button>

        <el-button icon="iconfont icon-pass" type="primary" @click="audit" size="mini" v-if="authority.b_pass==1">审核</el-button>
        <el-button plain @click="AUDIT_GET" size="mini" v-if="authority.b_nopass==1">弃审</el-button>
        <el-button plain icon="iconfont icon-zuofei" @click="cancellation" size="mini" v-if="authority.b_invalid==1">作废</el-button>
      </div>
      <div class="table_container">
        <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" v-loading="loading1" style="width: 100%" @selection-change="handleSelectionChange" max-height="600">
          <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44">
          </el-table-column>
          <el-table-column label="操作" fixed="right" width="100">
            <template slot-scope="scope">
              <el-button @click="seeDetail(scope, scope.row)" type="text" size="small" v-if="authority.b_info==1">
                查看
              </el-button>
              <el-button @click="addF(scope, scope.row)" type="text" size="small" v-if="scope.row.auditor_status==1 && authority.b_edit==1">
                修改
              </el-button>
              <el-button @click="addF(scope, scope.row)" disabled type="text" size="small" v-else>
                修改
              </el-button>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="company_name" width="150" label="库存组织">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="outstock_sn" width="180" label="出库单号">
          </el-table-column>

          <el-table-column :show-overflow-tooltip="true" property="customer_name" width="120" label="客户名称">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="add_user_name" width="150" label="操作员">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="auditor_status_name" width="100" label="审核状态">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="auditor_name" width="150" label="审核人">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="business_time" width="180" label="业务日期">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="add_time" width="180" label="单据时间">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="auditor_time" width="180" label="审核时间">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="abstract" label="备注">
          </el-table-column>
        </el-table>
      </div>
      <div class="page_container">
        <div class="pageNumber">每页显示</div>
        <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" :page-sizes="per_page" @current-change="handleCurrentChange" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import {
  getData,
  CompanySwitchClear,
  opthionList
} from "common/utils/public_function/commonUtils";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "instorageOldtyresstorageTirebackstorageIndex",
  data() {
    return {
      // authority: {},
      lock: 0,
      pages: 1,
      loading:false,
      showDepartment: true
    };
  },
  computed: {
    ...mapGetters(["tirebackstorageIndexGet"])
  },
  methods: {
    ...mapActions([
      "tirebackstorageAdd",
      "tirebackstorageSee",
      "tirebackIndexACT"
    ]),
    departmentChange(val) {
      if (val != "") {
        this.params.department_name = this.departmentList.filter(
          obj => obj.department_id === val
        )[0].department_name;
      }
    },
    // wz alter
    customerListChange(val) {
      if (val) {
        this.params.customer_name = this.customerList.filter(
          obj => obj.customer_id === val
        )[0].customer_name;
      }
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.$set(this, "params", {
        outstock_type_id: 10008,
        auditor_status: 0
      });
      this.timeValue = [];
      this.$set(this, "searchParams", {
        params: {
          outstock_type_id: 10008,
          auditor_status: 0
        },
        TableData: this.TableData, //列表数据
        API: "getOutstockList", //接口名
        vm: this //传递的this
      });
      // getData(this.searchParams);
      this.onSearch();
    },
    AUDIT_GET() {
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要弃审的信息",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].outstock_id);
      }
      var rebateArr = addr.toString();
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 15,
          bill_id: rebateArr,
          operation_type: 5
        })
        .then(() => {
          this.$axMethod
            .oldTireAuditorNoPass({
              outstock_id: rebateArr
            })
            .then(res => {
              if (res.data.code == 200) {
                this.$message({
                  message: res.data.msg,
                  type: "success"
                });
                getData(this.searchParams); //获取销售订单列表
              }
            });
          //弃审
        });
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    companyChange(val) {
      if (val != "") {
        this.params.company_name = this.companyList.filter(
          obj => obj.company_id === val
        )[0].company_name;
      }
      CompanySwitchClear(this, this.params);
    },
    onSearch() {
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.business_start_time = this.timeValue[0];
        this.params.business_end_time = this.timeValue[1];
      } else {
        this.params.business_start_time = this.params.business_end_time = "";
      }
      Object.assign(this.searchParams.params, this.params, {
        page: 1
      });

      this.tirebackIndexACT({
        goPage: this.goPage,
        params: this.params,
        pageSize: this.pageSize
      });
      getData(this.searchParams);
    },
    audit() {
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要审核的信息",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].outstock_id);
      }
      var rebateArr = addr.toString();
      var funcan = {
        examineID: "outstock_id",
        eachData: {
          outstock_id: rebateArr
        },
        examineApi: "getauidtorPass",
        list: {
          UrlApi: "getOutstockList",
          data: this.TableData,
          esData: this.searchParams.params
        }
      };
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 15,
          bill_id: rebateArr,
          operation_type: 4
        })
        .then(() => {
          this.$common.examineFun(funcan);
        });
    },
    //作废
    cancellation() {
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要作废的信息",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].outstock_id);
      }
      var rebateArr = addr.toString();
      var funcan = {
        examineID: "outstock_id",
        eachData: {
          outstock_id: rebateArr
        },
        examineApi: "getauditorInvalid",
        list: {
          UrlApi: "getOutstockList",
          data: this.TableData,
          esData: this.searchParams.params
        }
      };
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 15,
          bill_id: rebateArr,
          operation_type: 6
        })
        .then(() => {
          this.$common.examineFun(funcan);
        });
    },
    //删除
    deletes() {
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要作废的信息",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].outstock_id);
      }
      var rebateArr = addr.toString();
      var deledata = {
        deleID: "outstock_id",
        deleSelect: this.multipleSelection,
        deleApi: "getdelOutstock",
        list: {
          UrlApi: "getOutstockList",
          data: this.TableData,
          esData: this.searchParams.params
        }
      };
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 15,
          bill_id: rebateArr,
          operation_type: 3
        })
        .then(() => {
          this.$common.deletes(deledata, "删除", this);
        });
    },
    addF(scope, row) {
      if (row && row.is_execute == 1) {
        this.$message.error("wms系统正在操作此单据，暂不支持单据修改");
        return false;
      }
      this.$router.push({
        path: "/inventory/tireback/tirebackAdd"
      });
      this.tirebackstorageAdd({
        goPage: this.goPage,
        params: this.params,
        pageSize: this.pageSize,
        outstock_id: row ? row.outstock_id : ""
      });
    },
    seeDetail(scope, row) {
      this.$router.push({
        path: "/inventory/tireback/tirebackSee"
      });
      this.tirebackstorageSee({
        outstock_id: row.outstock_id,
        goPage: this.goPage,
        params: this.params,
        pageSize: this.pageSize
      });
    }
  },
  mounted() {
    this.$set(this, "params", {
      outstock_type_id: 10008,
      auditor_status: 0
    });
    if (this.tirebackstorageIndexGet && this.tirebackstorageIndexGet.params) {
      if (this.tirebackstorageIndexGet.params.business_start_time) {
        const {
          business_start_time,
          business_end_time
        } = this.tirebackstorageIndexGet.params;
        this.timeValue = this.timeValue.concat([
          business_start_time,
          business_end_time
        ]);
      }
      this.$set(this, "params", this.tirebackstorageIndexGet.params);
      opthionList(this, this.tirebackstorageIndexGet.params);
    }
    if (this.tirebackstorageIndexGet.goPage) {
      this.params.page = this.tirebackstorageIndexGet.goPage;
      this.params.per_page = this.tirebackstorageIndexGet.pageSize;
    }
    this.$set(this, "searchParams", {
      params: this.params, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getOutstockList", //接口名
      vm: this //传递的this
    });
    getData(this.searchParams);
  }
};
</script>

